@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-400.woff2") format("woff2"), url("../fonts/montserrat-400.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-500.woff2") format("woff2"), url("../fonts/montserrat-500.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-600.woff2") format("woff2"), url("../fonts/montserrat-600.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-700.woff2") format("woff2"), url("../fonts/montserrat-700.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", "Helvetica CY", "Nimbus Sans L", sans-serif;
  color: #011c40;
  scroll-behavior: smooth;
  min-width: 320px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #f9fbfd;
}

main {
  flex: auto;
}

a {
  color: #011c40;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.btn {
  display: inline-flex;
  vertical-align: baseline;
  padding: 10px 20px;
  color: #000000;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
.btn:hover {
  opacity: 0.8;
}

.header {
  position: relative;
  z-index: 2;
  background: #011c40;
  background-position: top left;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  background-image: image-set("../img/background/header-1.webp" 1x, "../img/background/header-1.jpg" 1x, "../img/background/header-1@2x.webp" 2x, "../img/background/header-1@2x.jpg" 2x);
}
@media (max-width: 1023px) {
  .header {
    box-shadow: none;
    background-image: image-set("../img/background/header-2.webp" 1x, "../img/background/header-2.jpg" 1x, "../img/background/header-2@2x.webp" 2x, "../img/background/header-2@2x.jpg" 2x);
  }
}
@media (max-width: 767px) {
  .header {
    min-height: 500px;
    background-image: image-set("../img/background/header-3.webp" 1x, "../img/background/header-3.jpg" 1x, "../img/background/header-3@2x.webp" 2x, "../img/background/header-3@2x.jpg" 2x);
  }
}

.header__wrapper {
  padding: 35px clamp(29px, 5.1244509517%, 103px) 277px;
  margin: 0 auto;
  max-width: 1366px;
}
@media (max-width: 1023px) {
  .header__wrapper {
    padding: 30px clamp(10px, 5.859375%, 45px) 275px;
  }
}
@media (max-width: 767px) {
  .header__wrapper {
    padding: 20px clamp(15px, 4.6875%, 30px) 10px;
  }
}

.header__menu {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .header__menu {
    display: flex;
    flex-direction: row;
  }
}

.header__logo {
  min-width: 70px;
  margin: 0 33px;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .header__logo {
    min-width: 38px;
    margin: 0 10px 0 0;
  }
}
@media (max-width: 767px) {
  .header__logo {
    margin-bottom: 10px;
  }
}
.header__logo svg {
  display: block;
}
@media (max-width: 1023px) {
  .header__logo svg {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .header__logo svg {
    width: 42px;
    height: 22px;
  }
}

.header__nav {
  display: flex;
}
@media (max-width: 767px) {
  .header__nav {
    justify-content: flex-end;
  }
}

.header__nav-toggle {
  display: none;
  position: relative;
  width: 24px;
  height: 12px;
  padding: 10px;
  background: none;
  border: none;
}
.header__nav-toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #f9fbfd;
  box-shadow: 0 6px 0 0 #f9fbfd, 0 12px 0 0 #f9fbfd;
}

.header__list {
  display: flex;
  flex-wrap: wrap;
  margin-right: 15px;
}
@media (max-width: 1023px) {
  .header__list {
    margin-right: -12px;
  }
}
@media (max-width: 767px) {
  .header__list {
    margin: 0;
  }
}

.header__list-item {
  display: flex;
  align-content: center;
}
@media (max-width: 767px) {
  .header__list-item {
    width: 100%;
    margin-bottom: 10px;
  }
}
.header__list-item a {
  display: inline-flex;
  vertical-align: baseline;
  padding: 9px 15px;
  margin: 0 2px;
  font-weight: 500;
  font-size: 16px;
  line-height: 125%;
  color: #f9fbfd;
}
.header__list-item a:hover {
  color: #78bcfc;
}
@media (max-width: 1023px) {
  .header__list-item a {
    padding: 3px 10px;
    font-size: 14px;
    line-height: 122%;
  }
}
@media (max-width: 767px) {
  .header__list-item a {
    display: flex;
    justify-content: center;
    width: 100%;
    font-size: 18px;
    padding: 10px 8px 8px;
  }
}

.header__promo {
  margin: 0;
  padding: 198px clamp(35px, 10.4685212299%, 140px) 0;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .header__promo {
    padding: 215px 0 0;
  }
}
@media (max-width: 767px) {
  .header__promo {
    padding: 124px 0 0;
  }
}
.header__promo h1 {
  padding: 0;
  margin: 0 0 12px 0;
  font-weight: 700;
  font-size: 54px;
  line-height: 122%;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .header__promo h1 {
    text-shadow: none;
    font-size: 50px;
  }
}
@media (max-width: 767px) {
  .header__promo h1 {
    font-size: 36px;
    line-height: 125%;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  }
}
.header__promo p {
  padding: 0;
  margin: 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 120%;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .header__promo p {
    text-shadow: none;
    font-size: 18px;
  }
}
@media (max-width: 767px) {
  .header__promo p {
    font-size: 16px;
    line-height: 140%;
    padding-right: 60px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  }
}

@media (max-width: 767px) {
  [data-header].is-js .header__wrapper {
    flex-direction: row;
    padding: 20px clamp(15px, 4.6875%, 30px) 177px;
  }
  [data-header].is-js .header__nav-toggle {
    display: block;
    margin-top: 4px;
  }
  [data-header].is-js .header__list {
    display: none;
  }
  [data-header].is-js .header-promo {
    padding: 124px 0 0;
  }
  [data-header].is-js h1,
[data-header].is-js p {
    text-shadow: none;
  }
}

@media (max-width: 767px) {
  [data-header].is-open {
    position: relative;
    min-height: 500px;
  }
  [data-header].is-open::before {
    content: attr(data-header-before);
    position: absolute;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 43, 102, 0.5);
  }
  [data-header].is-open .header__menu {
    position: absolute;
    right: 0;
    top: 0;
    width: 89.0625%;
    height: 100vh;
    box-sizing: border-box;
    margin: 0 0 0 auto;
    padding: 20px 11px 11px 0;
    background: #f9fbfd;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    overflow-y: auto;
  }
  [data-header].is-open .header__logo {
    margin-left: 15px;
  }
  [data-header].is-open svg {
    color: #011c40;
  }
  [data-header].is-open .header__nav {
    position: relative;
    width: 100%;
    padding-left: 15px;
  }
  [data-header].is-open .header__nav-toggle {
    position: absolute;
    top: -35px;
    right: 0;
    width: 20px;
  }
  [data-header].is-open .header__nav-toggle::before, [data-header].is-open .header__nav-toggle::after {
    content: "";
    position: absolute;
    top: 9px;
    left: 1px;
    width: 18px;
    height: 2px;
    background-color: #011c40;
  }
  [data-header].is-open .header__nav-toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  [data-header].is-open .header__nav-toggle::after {
    transform: rotate(-45deg);
  }
  [data-header].is-open .header__list {
    display: flex;
    flex-direction: column;
    flex: auto;
    margin: 24px 0 0 0;
  }
  [data-header].is-open a {
    justify-content: flex-start;
    color: #011c40;
  }
  [data-header].is-open a:hover {
    color: #78bcfc;
  }
  [data-header].is-open .header__promo {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    white-space: nowrap;
    border: 0;
    clip: rect(0 0 0 0);
    clip-path: inset(100%);
  }
}

.description {
