}
input[type=checkbox]:invalid:focus + span::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 10px;
  background-color: #ffffff;
  top: 20px;
  left: 0;
  clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
}

.feedback__checkbox {
  margin-left: 23px;
}
@media (max-width: 767px) {
  .feedback__checkbox {
    margin-left: 27px;
  }
}
.feedback__checkbox label {
  display: block;
  font-weight: 400;
  font-size: 10px;
  line-height: 130%;
  position: relative;
}
@media (max-width: 767px) {
  .feedback__checkbox label {
    line-height: 120%;
    padding: 0 50px 0 0;
  }
}
.feedback__checkbox span {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: -23px;
  box-sizing: border-box;
  width: 18px;
  height: 18px;
  background-color: #f9fbfd;
}
@media (max-width: 767px) {
  .feedback__checkbox span {
    top: 0;
    transform: translateY(0);
    left: -27px;
    width: 20px;
    height: 20px;
  }
}

